*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.content{
  height: 100vh;
}
.resgister-page{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
 /* Replace with your image path */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

/*transectionpage  */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}

.switch-icons{
  border : 1px solid rgba(0, 0,0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg{ 
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: black;
}
.inactive-icon{
  color: gray;
}

.login-form-container {
  background-color: rgba(178, 108, 47, 0.85); /* Add transparency to the background */
  padding: 30px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

.h1 {
  text-align: center;
  margin-bottom: 20px;
}

.outer-div { 
    text-align: center;
    display: ruby;
}
.register-link {
  color: #b2b9c0; /* Light blue color */
  text-decoration: none; /* Remove underline */
}

.register-link:hover {
  color: #b37700; /* Darker blue color on hover */
}